.container {
  margin: auto;
  min-height: 100vh;
}

/* //////FOR HOME SECTION///// */

body {
  background-attachment: fixed;
  background-image: url("../3147.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.Main {
  position: relative;
  display: grid;
  /* width:70vw; */
  margin: auto;
  grid-template-areas: "Main-header" "Main-intro";
}

.Main-header {
  grid-area: Main-header;
  position: relative;
  background-color: var(--color-yellow);
  height: 50vh;
  top: -20vh;
  color: white;
  font-family: selima;
  -webkit-transform: skew(0deg, 10deg);
      -ms-transform: skew(0deg, 10deg);
          transform: skew(0deg, 10deg);
  -webkit-box-shadow: 0em 1em 1em gray;
          box-shadow: 0em 1em 1em gray;
}

.Main-logo {
  grid-area: Main-header;
  background-image: url("../chi-resume-logo.png");
  width: 25vw;
  height: 25vw;
  background-size: cover;
  margin: auto;
  z-index: 9;
  -webkit-transform: translate(15vw, 5vh);
      -ms-transform: translate(15vw, 5vh);
          transform: translate(15vw, 5vh);
  -webkit-box-shadow: 0.3em 0.5em 2em gray;
          box-shadow: 0.3em 0.5em 2em gray;
  border-radius: 50%;
}

.Main-intro {
  position: absolute;
  left: 40vw;
  grid-area: Main-intro;
  /* font-size:2vw; */
  font-size: calc(1.2em + 1vw);
  font-family: 'Montserrat', serif;
  -webkit-transform: translate(-20vw, -10vh);
      -ms-transform: translate(-20vw, -10vh);
          transform: translate(-20vw, -10vh);
}

.Main h1 {
  font-family: baka;
  font-size: 2em;
  font-weight: 500;
  -webkit-animation: floating-text infinite ease-in-out 2000ms alternate;
          animation: floating-text infinite ease-in-out 2000ms alternate;
}

.Main-skill {
  position: relative;
  display: grid;
  grid-template-areas: "skill-h1";
  width: 70vw;
  margin: auto;
  -webkit-transform: translate(0vw, 10vh);
      -ms-transform: translate(0vw, 10vh);
          transform: translate(0vw, 10vh);
  font-size: 1em;
}

.Main-skill>h1 {
  grid-area: skill-h1;
  color: var(--color-red);
  font-weight: bold;
  text-align: middle;
}

.Main-skill ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  -webkit-transform: skew(0deg, -5deg);
      -ms-transform: skew(0deg, -5deg);
          transform: skew(0deg, -5deg);
}

.Main-skill ul li {
  display: inline-block;
  font-size: calc(1.5em + 1vw);
  font-family: baka;
  margin: 0.5em;
  -webkit-transform: skew(0deg, -5deg);
      -ms-transform: skew(0deg, -5deg);
          transform: skew(0deg, -5deg);
  -webkit-text-decoration: underline var(--color-red);
          text-decoration: underline var(--color-red);
}

.Floating-1 {
  -webkit-animation: floating-text infinite ease-in-out 2000ms alternate;
          animation: floating-text infinite ease-in-out 2000ms alternate;
}

.Floating-2 {
  -webkit-animation: floating-text infinite ease-in-out 1800ms alternate;
          animation: floating-text infinite ease-in-out 1800ms alternate;
}

.Floating-3 {
  -webkit-animation: floating-text infinite ease-in-out 1600ms alternate;
          animation: floating-text infinite ease-in-out 1600ms alternate;
}

.Floating-4 {
  -webkit-animation: floating-text infinite ease-in-out 1400ms alternate;
          animation: floating-text infinite ease-in-out 1400ms alternate;
}

@-webkit-keyframes floating-text {
  0% {
    -webkit-transform: translateY(.2em);
            transform: translateY(.2em);
    text-shadow: 0 0.2em 0.2em gray;
  }
  100% {
    /* transform: translateY(.2em); */
    text-shadow: 0 0.5em 0.5em gray;
  }
}

@keyframes floating-text {
  0% {
    -webkit-transform: translateY(.2em);
            transform: translateY(.2em);
    text-shadow: 0 0.2em 0.2em gray;
  }
  100% {
    /* transform: translateY(.2em); */
    text-shadow: 0 0.5em 0.5em gray;
  }
}

/* /////FOR PORTFOLIO SECTION////// */

.portfolio {
  display: grid;
  color: black;
  background-color: var(--color-yellow);
  -webkit-transform: skew(0deg, -10deg);
      -ms-transform: skew(0deg, -10deg);
          transform: skew(0deg, -10deg);
  -webkit-box-shadow: 0em -1em 1em gray, 0em 1em 1em gray;
          box-shadow: 0em -1em 1em gray, 0em 1em 1em gray;
  ;
}

.Portfolio-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 70vw;
  margin: auto;
  -webkit-transform: skew(0deg, 10deg);
      -ms-transform: skew(0deg, 10deg);
          transform: skew(0deg, 10deg);
}

.Portfolio-logo {
  display: grid;
  width: 15vw;
  height: 15vw;
  background-image: url('../chi-portfolio-logo.PNG');
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-box-shadow: 0.3em 0.5em 2em gray;
          box-shadow: 0.3em 0.5em 2em gray;
  border-radius: 5%;
  align-self: center;
}

.Tilt-inner {
  display: grid;
  grid-template-areas: "tile-h1" "tile-center";
  cursor: pointer;
}

.Portfolio-grid h1 {
  color: var(--color-metal);
  grid-area: tile-h1;
  font-family: baka;
  font-size: calc(1.5em + 1vw);
  -webkit-text-decoration: underline var(--color-red);
          text-decoration: underline var(--color-red);
}

.Desktop {
  grid-area: tile-center;
  width: 32vw;
  height: 18vw;
  -webkit-box-shadow: 0.2em 0.5em 0.5em gray;
          box-shadow: 0.2em 0.5em 0.5em gray;
  border-radius: 3%;
}

.Portfolio-grid p {
  opacity: 0;
  grid-area: tile-center;
  font-family: 'Montserrat', sans-serif;
  font-size: calc(1em + 0.5vw);
  font-weight: bold;
  color: white;
  margin: auto;
  -webkit-transition-duration: 800ms;
       -o-transition-duration: 800ms;
          transition-duration: 800ms;
}

.Tilt-inner:hover p {
  opacity: 1;
}

/* /////FOR CONTACTS SECTION////// */

.Contact {
  position: relative;
  font-family: baka;
  display: grid;
  width: 70vw;
  margin: auto;
  font-family: 'Montserrat', sans-serif;
  -ms-flex-align: center;
      align-items: center;
}

.Contact-logo {
  grid-area: "contact-logo";
  width: 13vw;
  height: 13vw;
  background-image: url('../chi-contact-logo.png');
  background-size: contain;
  background-repeat: no-repeat;
  justify-self: end;
  -webkit-box-shadow: 0.3em 0.5em 2em gray;
          box-shadow: 0.3em 0.5em 2em gray;
  border-radius: 5%;
}




